<template>
  <div class="create">
    <div class="header">
      标题：<el-input type="text" placeholder="输入标题" v-model="paperInfo.title" style="max-width: 250px;"
        @blur="saveTable()" />
      <el-button style="float: right" @click="saveTable()">保存</el-button>
    </div>

    <el-divider/>

    <el-form>
      <el-form-item style="text-align: left" ref="questionList" label-width="100px"
        v-for="(ques, index) in questionList" :key="index">
        <el-input type="textarea" placeholder="请输入问题" v-model="ques.title"
          style="width: 700px; border: 2px solid; display: block">
        </el-input>
        <div v-if="ques.type === 'FILL_IN'">
          <div>
            <el-tag type="info" style="margin-top: 10px">填空</el-tag>
          </div>
          <el-input type="textarea" placeholder="回答"
            style="display: block; width: 500px; margin:10px 0; padding-right: 400px;">
          </el-input>
        </div>
        <div v-else-if="ques.type === 'SINGLE_ANSWER_MULTIPLE_CHOICE'">
          <el-tag type="info" style="margin-top: 10px">单选</el-tag>
          <el-button style="display: block; margin-top: 10px" type="primary" size="mini" icon="el-icon-plus"
            @click="addOption(ques)">
            新增选项
          </el-button>
          <div style="padding-right: 600px; margin-top: 10px;" v-for="(option, index) in ques.options" :key="index">
            <el-radio style="display: inline-block" disabled=""></el-radio>
            <el-input style="display: inline-block; width: 200px" v-model="option.value"></el-input>
            <el-button type="danger" icon="el-icon-close" circle size="mini" style="margin-left: 5px"
              @click="removeOption(ques, index)">
            </el-button>
          </div>
        </div>
        <div v-else-if="ques.type === 'MULTIPLE_ANSWER_MULTIPLE_CHOICE'">
          <el-tag type="info" style="margin-top: 10px">多选</el-tag>
          <el-button 
            style="display: block; margin-top: 10px"
            type="primary"
            size="mini"
            icon="el-icon-plus"
            @click="addOption(ques)">
            新增选项
          </el-button>
          <div
            style="padding-right: 600px;"
            v-for="(option, index) in ques.options"
            :key="index"
          >
            <el-checkbox
              style="display: inline-block; margin-right: 10px"
              disabled=""/>
            <el-input
              style="display: inline-block; width: 200px"
              v-model="option.value"/>
            <el-button
              type="danger"
              icon="el-icon-close"
              circle size="mini"
              style="margin-left: 5px"
              @click="removeOption(ques, index)"/>
          </div>
        </div>
        <div v-else-if="ques.type === 'LIST_QUESTION'">
          <div>
            <el-tag type="info" style="margin-top: 10px">列表</el-tag>
          </div>
          <el-table :data="defaultData" border style="width: 50%" @selection-change="handleSelectionChange"
            tooltip-effect="dark">
            <el-table-column prop="data" label="数据"/>
          </el-table>
        </div>
        <div>
          <p> </p>
        </div>
        <div style="display: flex; justify-content: flex-start;">
          <el-button @click="delQues(index)" type="danger">删除</el-button>
          <div style="flex: 0 1 20%"></div>
        </div>
        <el-divider />
      </el-form-item>
    </el-form>
    <!--<div style="margin-bottom: 30px; padding-left: 700px; padding-bottom: 30px">

    </div>-->
    <div style="padding-bottom: 20px">
      <el-divider v-if="Listempty" direction="vertical" />
      <el-dropdown @command="addQues">
        <el-button type="primary">新增问题<i class="el-icon-arrow-down el-icon--right"></i></el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="FILL_IN">
            填空
          </el-dropdown-item>
          <el-dropdown-item command="SINGLE_ANSWER_MULTIPLE_CHOICE" divided>
            单选
          </el-dropdown-item>
          <el-dropdown-item command="MULTIPLE_ANSWER_MULTIPLE_CHOICE" divided>
            多选
          </el-dropdown-item>
          <el-dropdown-item command="LIST_QUESTION" divided>
            列表
          </el-dropdown-item>
          <el-dropdown-item command="TABLE_QUESTION" divided>
            表格
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'

export default {
  name: 'Create',
  data() {
    return {
      datepicker: false,
      radio: '1',
      date: ''
    }
  },
  async mounted() {
    console.log('Create with paperInfo & questionList')
    const tableId = this.$route.params.tableId
    console.log(`tableId: ${tableId}`)
    await this.editTable(tableId)
  },
  watch: {
    paperInfo() {
      if (this.paperInfo.startTime != null) {
        this.datepicker = true
        this.radio = '2'
        this.date = [
          new Date(this.paperInfo.startTime),
          new Date(this.paperInfo.endTime)
        ]
      }
    }
  },
  computed: {
    ...mapGetters(['paperInfo', 'questionList']),
    Listempty() {
      return this.questionList.length !== 0
    },
  },
  methods: {
    ...mapActions([
      'createQuestion',
      'updateQuestion',
      'deleteQuestion',
      'editTable',
      'updateTable',
      'releaseTable'
    ]),
    saveTable() {
      console.log('invoke saveTable')
      this.updateTable(this.paperInfo).then((res) => {
        if (res.success) {
          this.$notify.success('保存成功')
          this.$router.push({ name: 'overview' })
        }
        else {
          this.$notify.error(res.message)
        }
      })
    },
    addQues(type) {
      this.createQuestion(type)
    },
    delQues(index) {
      this.deleteQuestion(index)
    },
    checkQues(ques) {
      if (ques.title === '') {
        this.$message.warning('题干不应为空')
        return false
      }
      if (ques.type <= 2) {
        if (ques.options.length < 2) {
          this.$message.warning('选择题至少应有两个选项')
          return false
        }
        // 检查选项
        for (let option of ques.options) {
          if (option.length === 0) {
            this.$message.warning('选项不可为空')
            return false
          }
        }
      }
      return true
    },
    addOption(question) {
      question.options.push({ value: '' })
    },
    removeOption(question, index) {
      question.options.splice(index, 1)
    },
    submitTable: async function () {
      console.log(`submitTable with tableId: ${this.paperInfo.id}`)

      // 跟更新采集表状态
      
      const res = await this.releaseTable()
      console.log(res)
      if (res) {
        this.$message.success(`发布成功`)
      } else {
        this.$message.error(`发放采集表异常`)
      }
    }
  }
}
</script>

<style>
.header {
  height: 50px;
  padding: 0 20px;
  line-height: 47px;
  font-size: 20px;
  text-align: left;
}

.name {
  float: left;
  font-size: 20px;
}

.el-checkbox-group {
  padding-right: 700px;
}

.el-checkbox {
  display: block;
  margin: 10px;
}

.el-checkbox:last-of-type {
  margin: none;
}

.el-radio {
  margin: 0;
}
</style>
